.categorybox {
    overflow: hidden;
    /*cursor: pointer;*/
    border: 3px solid;
    border-color: #f5f5f5;
    background-color: #ffffff;
    padding: 10px;
    position: relative;
    border-radius: 10px;
    width: 70%;
    height: 20%;
    margin: 0 auto;
}

.accordionTitle {
  padding: 5px;
}

.avatar {
  float: left;
  width: 20px;
  height: 20px;
  padding: 0px;
  background-color: transparent;
  /*border-radius: 20px;*/
}

.ctitleLabel {
  display: inline;
  margin-left: 10px;
  font-size: larger;
  font-weight: bolder;
}

.categoryContent {
    display: flex;
    align-items: center;
    align-self: center;
    gap: 5px;
    height: 19px;
    position: relative;
    width: fit-content;
    margin: 5px 10px;  /* 위아래, 좌우 */
}

