ul {
  list-style: none;
  padding-left: 0;
}
il{
  top: 5px;
  left: 5px;
}
#leftToRight li::after {
  position: absolute; 
  content:""; display: block; 
  border-bottom: 3px solid #5d1fc2; 
  transition: width 250ms ease-out; 
  left: auto; right: 0;  width: 0;
}
#leftToRight li:hover::after {
  width: 100%; left: 0; right: auto;
}

.contentBtn {
    background-color: transparent;
    border: none;
    
    font-family: "Inter", Helvetica;
    font-size: 14px;
    list-style: none;

    /* 여기서부터 css 코드 */
    position: relative;
    transition: 0.3s;
}
.contentBtn:hover {
  /*color: 텍스트 색깔*/
  letter-spacing: 3px;
  /*background: rgb(234, 235, 235);*/
}



/* 아코디언 버튼 스타일 */

.categorybox button {
    width: 100%;
    height: 40px;
    margin-top: 10px;
    background-color: #ebeaea;
    color: #000000;
    border: none;
    padding: 5px 10px;
    border-radius: 3px;
    cursor: pointer;
    /*font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;*/
    font-family: "Noto Sans KR";
    font-size: large;
  }
  
  .categorybox button:hover {
    background-color: #dcdcdc;
  }
  
  /* 아코디언 콘텐츠 스타일 */
  .categorybox .accordionContent {
    display: flex;
    flex : 2;
    flex-wrap: wrap;
    align-content: space-around;
    background-color: transparent;
    /*border: 1px solid #ebeaea;*/
    padding: 10px;
    border-radius: 3px;
  }
